.share-title {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 32px;
  color: #000;
}
.personaldata-title {
  border: 1px solid rgba(0, 0, 0, 0.06);
  box-shadow: 0px 1px 3px rgba(34, 25, 25, 0.2);
  border-radius: 8px;
  padding: 24px 32px 18px 32px;
  margin-bottom: 16px;
}
.personaldata-title .ant-input {
  height: 48px;
}
.personaldata-title .ant-form-item {
  margin-bottom: 20px;
}
@media only screen and (max-width: 768px) {
  .personaldata-title .ant-form-item {
    margin-bottom: 0px;
  }
}
.personaldata-content {
  border: 1px solid rgba(0, 0, 0, 0.06);
  box-shadow: 0px 1px 3px rgba(34, 25, 25, 0.2);
  border-radius: 8px;
  padding: 24px 32px;
}
.personaldata-content .ant-form-item {
  margin-bottom: 0;
}
.personaldata-content .ant-col {
  margin-bottom: 12px;
}
.personaldata-content .ant-input {
  height: 48px;
}
.personaldata-content .picker {
  width: 100%;
}
.personaldata-content .bb8 {
  margin-bottom: 8px;
}
.personaldata-content .login-form-button {
  float: right;
}
.personaldata-content .hide {
  margin-bottom: 0px;
}
.personaldata-content .token-list {
  width: 800px;
  background: #f5f5f5;
  border-radius: 4px;
  margin-bottom: 24px;
  padding: 0 17px;
  height: 55px;
  line-height: 55px;
}
.ledit {
  color: #fff;
  background-color: #0091FF;
  border-color: #0091FF;
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.12);
  box-shadow: 0 2px 0 rgba(0, 0, 0, 0.045);
  text-align: center;
  padding: 0 20px;
  float: right;
  border-radius: 4px;
  line-height: 2.5;
}
.ledit:hover {
  cursor: pointer;
}
.cancel {
  border-color: rgba(0, 0, 0, 0.25);
  float: right;
  margin-right: 20px;
}
@media only screen and (max-width: 768px) {
  .personaldata-content .token-list {
    width: 100%;
  }
}
